import { CSSProperties } from "react";
import { Styles as ReactModalCustomStyles } from "react-modal";

import { colors, spacingMap } from "../styleguide";

import { DrawerPositions, DrawerSizes, DrawerStylesProps } from "./Drawer.types";

const rightPositionStyles: CSSProperties = {
	top: spacingMap.xs,
	bottom: spacingMap.xs,
	right: spacingMap.xs,
	left: "auto",
};

const drawerPosition: Record<DrawerPositions, CSSProperties> = {
	right: rightPositionStyles,
};

export const drawerWidth: Record<DrawerSizes, string> = {
	small: "480px",
	medium: "600px",
	large: "960px",
	// Adjust the Drawer width to fit the window, accounting for the default left inset set to auto.
	fullScreen: "calc(100% - 1rem)",
};

export const getCustomDrawerStyles = ({
	position = "right",
	$zIndex = 99,
	size = "small",
}: DrawerStylesProps): ReactModalCustomStyles => ({
	overlay: {
		backgroundColor: "transparent",
		zIndex: $zIndex,
	},
	content: {
		width: drawerWidth[size],
		borderRadius: "16px",
		border: "none",
		borderTop: `1px solid ${colors.white}`,
		backgroundColor: colors.lightestGray,
		padding: 0,
		display: "flex",
		flexDirection: "column",
		boxShadow: `0px 2px 2px 0px rgba(0, 0, 0, 0.08),
			0px 4px 4px 0px rgba(0, 0, 0, 0.08), 0px 8px 16px -4px rgba(0, 0, 0, 0.08)`,
		top: spacingMap.xs,
		bottom: spacingMap.xs,
		right: spacingMap.xs,
		left: "auto",
		...drawerPosition[position],
	},
});
